import React, { Component } from "react";
import {
    Col, Button, Card, CardImg, CardBlock, CardTitle, CardSubtitle, Badge, CardText
} from "reactstrap";
import { Transform } from "stream";

export default class ProductItem extends Component {
    handleDiscount = (discount) => {
        return discount < 1 ? <Badge color="danger">${(this.props.data.price * discount).toFixed(2)}</Badge> : <Badge color="success">${this.props.data.price.toFixed(2)}</Badge>
    }

    render() {
        return (
            <Col sm={2} md={4} className="mb-3">
                <Card>
                    <CardImg top width="100%" src={this.props.data.picture} alt={this.props.data.name} />
                    <CardBlock>
                        <CardTitle className="clearfix" style={{ position: "absolute", top: "10px", padding: '.5rem' }}>{this.props.data.name}</CardTitle>
                        <CardSubtitle className="itemUp">
                            <h4 className="price"> {this.handleDiscount(this.props.data.discount)} </h4>
                            {this.props.data.discount !== 1 ? <CardText className="clearfix" style={{ padding: ".5rem", position:"absolute",right:"0px" }}>{this.props.data.discount * 100}% off</CardText> : ""}
                        </CardSubtitle>
                        <Button color="primary">Buy</Button>
                    </CardBlock>
                </Card>
            </Col>
        );
    }
}