import React, { Component } from "react";
import { Row, Container, Collapse, Button, CardBody, Card, Col } from "reactstrap";
import ProductItem from "./ProductItem";
//https://www.aliceandwhittles.com/collections/all
export default class ProductList extends Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state={
            collapse: false,
        };
    }
    toggle() {
        this.setState({ collapse: !this.state.collapse });
    }

    render(){
        let data = [{
                name: "Minimalist Black + White Ankle Boot",
                price: 96.00,
                discount: 0.75,
                picture: "https://cdn.shopify.com/s/files/1/0665/2889/products/aliceandwhittles_productimages-resized-01_2000x.jpg?v=1545821295"
            }, {
                name: "Minimalist Black Ankle Boot",
                price: 96.00,
                discount: 1,
                picture: "https://cdn.shopify.com/s/files/1/0665/2889/products/aliceandwhittles_productimages-resized-03_grande_22a017d0-8a31-4eb3-a921-083578f5a45b_2000x.jpg?v=1545391572",
            }, {
                name: "Classic Black Ankle Boot",
                price: 90.00,
                discount:0.5,
                picture:"https://cdn.shopify.com/s/files/1/0665/2889/products/aliceandwhittles_productimages-resized-04_2000x.jpg?v=1547723705"
            }]
        return (
            <Col xs={5} md={9}>
                <Row>
                    {
                        data.map(item => {
                            return (
                                <ProductItem className="productItem" data={item} />
                            )
                        })
                    }
                </Row>
            </Col>
        );
    }
}