import React, { Component } from "react";
import { Row, Container, Collapse, Button, CardBody, Card, Col } from "reactstrap";
import ProductItem from "./ProductItem";
//https://www.aliceandwhittles.com/collections/all
export default class SiderBar extends Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            collapse: false,
        };
    }
    toggle() {
        this.setState({ collapse: !this.state.collapse });
    }

    render() {
        return (
            <Col xs={2} md={3}>
                <Button onClick={this.toggle}
                    style={{ marginBottom: '1rem', background: "none", border: "none", color: "#000000" }}>Our Essentials
                </Button>
                <Collapse isOpen={this.state.collapse}>
                    <Card>
                        <CardBody>
                            Anim pariatur cliche reprehenderit,
                             enim eiusmod high life accusamus terry richardson ad squid. Nihil
                             anim keffiyeh helvetica, craft beer labore wes anderson cred
                             nesciunt sapiente ea proident.
                        </CardBody>
                    </Card>
                </Collapse>
                <hr />
                <Button onClick={this.toggle}
                    style={{ marginBottom: '1rem', background: "none", border: "none", color: "#000000" }}>Recently Viewed
                </Button>
                <Collapse isOpen={this.state.collapse}>
                    <Card>
                        <CardBody>
                            Anim pariatur cliche reprehenderit,
                             enim eiusmod high life accusamus terry richardson ad squid. Nihil
                             anim keffiyeh helvetica, craft beer labore wes anderson cred
                             nesciunt sapiente ea proident.
                        </CardBody>
                    </Card>
                </Collapse>
            </Col>
        );
    }
}