import React, { Component } from "react";
import { Row, Col, Card, CardImg, CardBody, CardText, Container, Form, FormGroup, Button, Input } from "reactstrap";

export default class CompanyInfo extends Component {
    handleService = info => {
        return (
            <Col >
                <Card style={{margin:".3rem"}}>
                    <Col md={2} className="align-self-center">
                        <CardImg top height="100%" src={info.picture} alt="Card image cap" />
                    </Col>
                    <CardBody>
                        <h5>{info.title}</h5>
                        <CardText>
                            {info.content.map(content => <p style={{ margin:".2rem" }}>{content}</p>)}
                        </CardText>
                    </CardBody>
                </Card>
            </Col>
        )
    }
    handleSubscribe = () => {
        return (
            <Container>
                <Col>
                    <Form>
                        <FormGroup>
                            <Input type="email" name="email" id="exampleEmail" placeholder="Enter your email address..." />
                        </FormGroup>
                        <Button style={{ height: "2.5rem", position: "relative", margin: "0.5rem" }}>Subscribe</Button>
                    </Form>
                </Col>
            </Container>
            )
    }
    render() {
        let service = [{
            title: "FREE SHIPPING TO US + CANADA",
            content: ["Get your boots within 6-8 business days"],
            picture:"https://cdn.shopify.com/s/files/1/0665/2889/files/aw_website_footericons-08_d6131863-8b13-48a4-9636-102a3eecb80a.png?3588873979952003109"
        }, {
            title: "GET IN TOUCH",
            content: ["Call us at 1-800-475-6479",
                      "Email us at info@aliceandwhittles.com",
                      "Chat with us live!"],
            picture:"https://cdn.shopify.com/s/files/1/0665/2889/files/aw_website_footericons-03_06cef792-0902-446d-8ca7-c40441a5445f.png?3588873979952003109"
        }, {
            title: "ONE YEAR WARRANTY",
            content: ["Wear your boots with ease"],
            picture:"https://cdn.shopify.com/s/files/1/0665/2889/files/aw_website_footericons-04_8baf725e-75d1-4a6a-a160-f0fa9fd3df09.png?3588873979952003109"
        }]
        return (
            <Container fluid>
                <Row>
                    {service.map(info => this.handleService(info))}
                </Row>
                <Card style={{margin:".5rem"}}>
                    <h5>Subscribe</h5>
                    <p>Get updates on new releases, sales and more.</p>
                    {this.handleSubscribe()}
                </Card>
            </Container>
        )
    }
}