import React, { Component } from "react";
import { Row, Col, Container, Card, CardText } from "reactstrap";
import { Route, Router } from "react-router";

export default class Footer extends Component {
    handleInfo = info => {
        info.service.map(item => {
             return (
                 <Card>
                     <CardText>{info.serviceTitle}</CardText>
                 </Card>
             )
         })
    }
    render() {
        let info = [{
            serviceTitle: "Our Shop",
            service: [{
                    title: "All Product",
                    link:""
            }, {
                    title: "Looks We Love",
                    link: ""
                }, {
                    title: "Ankle Boot Story",
                    link: ""
                }, {
                    title: "Riding Boot Story",
                    link: ""
                }]
        }]
        return (
            <Container>
                <Row>
                    {/*this.handleInfo(info)*/}
                </Row>
            </Container>
        )
    }
}